.box-1 {
  animation: appear;
  animation-duration: 1.5s;
}

.box-2 {
  animation: appear;
  animation-duration: 2.5s;
}

.box-3 {
  animation: appear;
  animation-duration: 3.5s;
}


@keyframes appear {
  from {opacity: 0 }
  to {opacity: 1 }
}

.Content {
  text-align: center;
}